
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Rater from 'vux/src/components/rater/index.vue'

import { getJsSdk } from '@/service/getData'
const list = () => ['', '', '', '', '']

export default {
components: {
Card,
Group,
Cell,
CellFormPreview,
Rater
},
data() {
return {
raterData: 5
}
},
mounted(){
this.initWX()
},
computed:{
raterDataDisplay(){
return '5.0'
}
},
methods: {
gotoDetial(){
this.$router.push({name: 'Detail'})
},
async initWX(){
var wxConfig = await getJsSdk({
url: window.location.href.split('#')[0]
})
var wx = this.$wechat;
console.log(1)
console.log(wxConfig)
wx.config({
debug: false, // ,apialertpclogpc
appId: wxConfig.appId, // 
timestamp: wxConfig.timestamp, // 
nonceStr: wxConfig.nonceStr, // 
signature: wxConfig.signature, // 1
jsApiList: wxConfig.jsApiList 
})
wx.ready(function () {
wx.checkJsApi({
jsApiList: ['chooseImage'], // JSJS2,
success: function(res) {
console.log(res)
}
});
wx.onMenuShareTimeline({
title: '',
desc: '',
link: window.location.href,
imgUrl: 'http://yy.machsys.cn/static/road.jpg'
});
wx.onMenuShareAppMessage({
title: '',
desc: '',
link: window.location.href,
imgUrl: 'http://yy.machsys.cn/static/road.jpg'
});
})

wx.error(function (res) {
console.log(res)

})
},
}
}



